            <button v-if="vdata.id" type="button" @click="$router.push(`/admin/nikah/acara?id=${vdata.id}`)" class="btn ml-3 btn-sm btn-danger"><span class="typcn typcn-archive"></span> Cara</button>
            <button v-if="vdata.id" type="button" @click="$router.push(`/admin/nikah/comment?id=${vdata.id}`)" class="btn ml-3 btn-sm btn-success"><span class="typcn typcn-message"></span> List Comment</button>
            <hr class="mt-1 mb-1">
